"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import { getAuth, isAuthenticated } from "../utils/authStorage";
import Logout from "../components/Logout";

export default function Home() {
  const [user, setUser] = useState<any>(null);

  useEffect(() => {
    if (isAuthenticated()) {
      const { user } = getAuth();
      setUser(user);
    }
  }, []);

  return (
    <main className="p-4">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-3xl font-bold mb-8 text-center">UTEShop Admin Panel</h1>
        
        {user ? (
          <div className="bg-green-50 p-6 rounded-lg mb-8">
            <div className="flex justify-between items-center">
              <div>
                <h2 className="text-xl font-semibold mb-2">Chào mừng, {user.fullName}!</h2>
                <p className="text-gray-600">Bạn đã đăng nhập thành công.</p>
              </div>
              <Logout />
            </div>
          </div>
        ) : (
          <div className="bg-blue-50 p-6 rounded-lg mb-8">
            <h2 className="text-xl font-semibold mb-4">Chào mừng đến với UTEShop Admin</h2>
            <div className="flex gap-4">
              <Link 
                href="/login" 
                className="bg-blue-500 text-white px-6 py-2 rounded-lg hover:bg-blue-600 transition"
              >
                Đăng nhập
              </Link>
              <Link 
                href="/register" 
                className="bg-green-500 text-white px-6 py-2 rounded-lg hover:bg-green-600 transition"
              >
                Đăng ký
              </Link>
            </div>
          </div>
        )}

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="bg-white p-6 rounded-lg shadow-md">
            <h3 className="text-lg font-semibold mb-2">Quản lý người dùng</h3>
            <p className="text-gray-600">Quản lý thông tin người dùng và phân quyền</p>
          </div>
          
          <div className="bg-white p-6 rounded-lg shadow-md">
            <h3 className="text-lg font-semibold mb-2">Quản lý sản phẩm</h3>
            <p className="text-gray-600">Quản lý danh mục và sản phẩm</p>
          </div>
          
          <div className="bg-white p-6 rounded-lg shadow-md">
            <h3 className="text-lg font-semibold mb-2">Quản lý đơn hàng</h3>
            <p className="text-gray-600">Theo dõi và xử lý đơn hàng</p>
          </div>
        </div>
      </div>
    </main>
  );
}
